<template>
  <div class="child"
       v-if="is_del">
    <div>
      <div class="child_row"
           @click="showChildren = !showChildren">
        <span class="child_companyName"
              :style="{paddingLeft: level*10+'px'}">
          <v-icon :style="{ visibility: companyObj.children&&companyObj.children.length > 0 ? 'visible' : 'hidden'}"
                  class="icon"
                  :class="{'transformIcon': showChildren}">mdi-menu-down</v-icon>
          {{INT(companyObj.org_level_name)}}
        </span>
        <menu-list @handleClick='handleClick'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </div>
      <div v-if="showChildren && companyObj.children.length > 0">
        <child-company v-for="item in companyObj.children"
                       :key='item.uuid'
                       :level='level+1'
                       @delData='delData'
                       :companyObj='item' />
      </div>
    </div>
  </div>
</template>

<script>
import { get_company_del } from '@/service/api/company_set'
export default {
  name: 'childCompany',
  components: {},
  props: {
    companyObj: {
      type: Object
    },
    level: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      is_del: true,
      showChildren: false,

    };
  },
  created () { },
  mounted () { },
  methods: {
    handleClick ({ item, index }) {
      switch (index) {
        case 0:
          this.$store.commit('set_subPage_name', this.companyObj.org_level_name)
          this.$store.commit('set_uuid', this.companyObj.uuid)
          this.$store.commit('setOrgLevelCode', this.companyObj.org_level_code)
          this.$store.commit('set_iconstatus', true)
          this.$router.push({
            name: 'enterpriseInfo',
            params: {
              name: this.companyObj.org_level_name,
              uuid: this.companyObj.uuid
            }
          })
          break;
        case 1:
          this.$store.commit('set_subPage_name', this.companyObj.org_level_name)
          this.$store.commit('set_uuid', this.companyObj.uuid)
          this.$store.commit('setOrgLevelCode', this.companyObj.org_level_code)
          this.$store.commit('set_iconstatus', false)
          this.$router.push({
            name: 'enterpriseInfo',
            params: {
              name: this.companyObj.org_level_name,
              uuid: this.companyObj.uuid
            }
          })
          break;
        case 2:
          this.$myConfirm({
            content: this.$t('message_Confirm_the_deletion')
          })
            .then(async () => {
              const del = await get_company_del({ uuid: this.companyObj.uuid })
              if (del.code == 200) {
                this.$emit('delData', this.companyObj.uuid)
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                await this.$store.dispatch('set_topcompanylist')
              }
            })
            .catch(() => { })
          break
      }
    },
    delData (uuid) {
      this.$emit('delData', uuid)
    }
  },
  computed: {
    // list () {
    //   return [
    //     {
    //       name: this.$t('label_Edit')
    //     },
    //     {
    //       name: this.$t('label_delete')
    //     }
    //   ]
    // }
  },
  watch: {},
};
</script>

<style scoped>
.child_row {
  width: 100%;
  height: 55px;
  border-top: 1px solid #c5c5c5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.child_companyName {
  font-size: 16px;
  color: #000000;
  display: flex;
  align-items: center;
  /* font-weight: 600; */
  /* color: #ff9100; */
  letter-spacing: 3px;
}
.icon {
  transform: rotate(-90deg);
}

.transformIcon {
  transform: rotate(0deg);
}
.child_row:hover {
  background-color: #e4dfdf;
}
</style>
