<template>
  <div onselectstart='return false'
       class="parent"
       v-if="is_del">
    <div class="parent_row"
         @click="showChildren = !showChildren">
      <div>
        <v-icon :style="{ visibility: companyObj.children&&companyObj.children.length > 0 ? 'visible' : 'hidden'}"
                class="icon"
                :class="{'transformIcon': showChildren}">mdi-menu-down</v-icon>
        <span class="parent_companyName">{{INT(companyObj.org_level_name)}}</span>
      </div>
      <menu-list @handleClick='handleClick'>
        <template v-slot:showAction>
          <v-icon>mdi-dots-vertical</v-icon>
        </template>
      </menu-list>
    </div>

    <div v-if="showChildren && companyObj.children.length > 0"
         style="margin-top: 10px;
      border: 1px solid #c5c5c5;
      border-top: none;
      border-radius: 7px;">
      <child-company v-for="item in companyObj.children"
                     :key='item.uuid'
                     :companyObj='item'
                     @delData='delData'
                     :level='1' />
    </div>

  </div>
</template>

<script>
import childCompany from './childCompany'
import { get_company_del } from '@/service/api/company_set'
export default {
  components: { childCompany },
  props: {
    companyObj: {
      type: Object
    }
  },
  data () {
    return {
      is_del: true,
      showChildren: false,

    };
  },
  created () { },
  mounted () { },
  methods: {
    delData (uuid) {
      this.$emit('delData', uuid)
    },
    handleClick ({ item, index }) {
      switch (index) {
        case 0:
          this.$store.commit('set_subPage_name', this.companyObj.org_level_name)
          this.$store.commit('set_uuid', this.companyObj.uuid)
          this.$store.commit('setOrgLevelCode', this.companyObj.org_level_code)
          this.$store.commit('set_uuid2', this.companyObj.client_uuid)
          this.$store.commit('set_iconstatus', true)
          this.$router.push({
            name: 'enterpriseInfo',
            params: {
              name: this.companyObj.org_level_name,
              uuid: this.companyObj.uuid
            }
          })
          break
        case 1:
          this.$store.commit('set_subPage_name', this.companyObj.org_level_name)
          this.$store.commit('set_uuid', this.companyObj.uuid)
          this.$store.commit('setOrgLevelCode', this.companyObj.org_level_code)
          this.$store.commit('set_uuid2', this.companyObj.client_uuid)
          this.$store.commit('set_iconstatus', false)
          this.$router.push({
            name: 'enterpriseInfo',
            params: {
              name: this.companyObj.org_level_name,
              uuid: this.companyObj.uuid
            }
          })
          break;
        case 2:
          this.$myConfirm({
            content: this.$t('message_Confirm_the_deletion')
          })
            .then(async () => {
              const del = await get_company_del({ uuid: this.companyObj.uuid })
              if (del.code == 200) {
                this.is_del = false
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                this.$emit('delData', this.companyObj.uuid)
                await this.$store.dispatch('set_topcompanylist')
              }
            })
            .catch(() => { })
          break
      }
    }
  },
  computed: {

  },
  watch: {},
};
</script>

<style scoped>
.parent {
  margin-bottom: 10px;
}
.parent_row {
  width: 100%;
  height: 56px;
  border: 1px solid #c5c5c5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
  padding: 0 10px;
}
.parent_companyName {
  font-size: 18px;
  font-weight: 600;
  color: #ff9100;
  letter-spacing: 3px;
}
.icon {
  transform: rotate(-90deg);
}
.transformIcon {
  transform: rotate(0deg);
}
.parent_row:hover {
  background-color: #e4dfdf;
}
</style>
